import React, { useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { db, auth, functions } from '@/firebase';
import { collection, addDoc, serverTimestamp } from 'firebase/firestore';
import { httpsCallable } from 'firebase/functions';
import { useToast } from '@/hooks/use-toast';
import { Link as LinkIcon, Bell } from 'lucide-react';

const AdminNotificationsPage: React.FC = () => {
  const { toast } = useToast();
  const [title, setTitle] = useState('');
  const [body, setBody] = useState('');
  const [link, setLink] = useState('');
  const [loading, setLoading] = useState(false);
  const [userId, setUserId] = useState('');

  const handleSend = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!title.trim() || !body.trim()) {
      toast({ title: 'خطأ', description: 'العنوان والنص مطلوبان', variant: 'destructive' });
      return;
    }
    try {
      setLoading(true);
      const payload = { title: title.trim(), body: body.trim(), link: link.trim() || '' };

      if (userId.trim()) {
        const fn = httpsCallable(functions, 'sendFcmToUser');
        const data: Record<string, string> = {};
        if (payload.link) data.link = payload.link;
        const res: any = await fn({ userId: userId.trim(), title: payload.title, body: payload.body, data });
        const ok = !!(res?.data?.ok);
        toast({ title: ok ? 'تم الإرسال' : 'تحذير', description: ok ? `أُرسل إلى المستخدم ${userId}` : 'لا توجد أجهزة مسجلة لهذا المستخدم', variant: ok ? undefined : 'destructive' });
      } else {
        await addDoc(collection(db, 'broadcasts'), {
          title: payload.title,
          message: payload.body,
          linkUrl: payload.link || undefined,
          audienceKey: 'global',
          active: true,
          createdAt: serverTimestamp()
        });
        toast({ title: 'تم الإرسال', description: 'تم إنشاء بث عام وسيتم إرسال الإشعار' });
      }
      setTitle('');
      setBody('');
      setLink('');
      setUserId('');
    } catch (err: any) {
      toast({ title: 'فشل الإرسال', description: String(err?.message || err), variant: 'destructive' });
    } finally {
      setLoading(false);
    }
  };

  const handleSendTestToMe = async () => {
    try {
      const uid = auth.currentUser?.uid || '';
      if (!uid) {
        toast({ title: 'غير مسجل', description: 'سجّل الدخول كأدمن لإرسال اختبار', variant: 'destructive' });
        return;
      }
      setLoading(true);
      const fn = httpsCallable(functions, 'sendFcmToUser');
      const res: any = await fn({ userId: uid, title: 'إشعار اختبار', body: 'تم إرسال إشعار تجريبي بنجاح', data: { type: 'test' } });
      const ok = !!(res?.data?.ok);
      toast({ title: ok ? 'تم إرسال الاختبار' : 'فشل الاختبار', description: ok ? 'تفقد هاتفك الآن' : 'تحقق من حفظ توكن الجهاز', variant: ok ? undefined : 'destructive' });
    } catch (err: any) {
      toast({ title: 'فشل الاختبار', description: String(err?.message || err), variant: 'destructive' });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen p-4">
      <Card className="max-w-xl mx-auto">
        <CardHeader>
          <CardTitle className="flex items-center gap-2 text-base">
            <Bell className="h-4 w-4" />
            إرسال إشعار خارجي
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <form onSubmit={handleSend} className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="userId">إرسال لمستخدم محدد (اختياري)</Label>
              <Input id="userId" value={userId} onChange={(e) => setUserId(e.target.value)} placeholder="أدخل معرف المستخدم (UID)" />
              <p className="text-xs text-gray-500">إذا تركته فارغاً سيتم إرسال إشعار عام للجميع.</p>
            </div>
            <div className="space-y-2">
              <Label htmlFor="title">العنوان</Label>
              <Input id="title" value={title} onChange={(e) => setTitle(e.target.value)} placeholder="عنوان الإشعار" />
            </div>
            <div className="space-y-2">
              <Label htmlFor="body">النص</Label>
              <Textarea id="body" value={body} onChange={(e) => setBody(e.target.value)} placeholder="نص الإشعار" className="min-h-[120px]" />
            </div>
            <div className="space-y-2">
              <Label htmlFor="link" className="flex items-center gap-1"><LinkIcon className="h-3 w-3" /> رابط اختياري</Label>
              <Input id="link" value={link} onChange={(e) => setLink(e.target.value)} placeholder="https://example.com أو cashy://..." />
            </div>
            <div className="flex justify-between">
              <Button type="button" onClick={handleSendTestToMe} disabled={loading} variant="outline">{loading ? '...' : 'إرسال اختبار لنفسي'}</Button>
              <Button type="submit" disabled={loading} className="px-6">{loading ? 'جاري الإرسال...' : 'إرسال'}</Button>
            </div>
          </form>
        </CardContent>
      </Card>
    </div>
  );
};

export default AdminNotificationsPage;
